<template>
  <div class="app-box">
    <div id="canvas_box" class="canvas_image_box">
      <div class="coinname">{{coinname}}</div>
      <div class="money">{{moneynum}}</div>
      <div class="money-box">
        <img :src="coinimg" class="logo-img" alt="">
        <div class="lt coin-name-font">{{coinname}}</div>
      </div>
      <div class="money-gt-box">
        <div class="money-first">{{coinnum}}</div>
        <div class="money-second">≈ ￥{{moneynum}}</div>
      </div>
    </div>
    <div class="select-center-box">
      <el-select class="select-box" v-model="coinname" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-input class="select-box" type="number" v-model="coinnum" placeholder="请输入金额"></el-input>
    </div>
    <div class="fool-btn" @click="getImage">
      导出图片
    </div>
  </div>


</template>

<script>
  import html2canvas from 'html2canvas';
  export default {
    name: "img",
    data(){
      return {
        options:[
          {
            value:'BTC',
            label:'BTC'
          },
          {
            value:'ETH',
            label:'ETH'
          },
          {
            value:'EOS',
            label:'EOS'
          },
          {
            value:'USDT',
            label:'USDT'
          },
          {
            value:'BCH',
            label:'BCH'
          },
          {
            value:'BSV',
            label:'BSV'
          },
          {
            value:'TRX',
            label:'TRX'
          },
          {
            value:'DASH',
            label:'DASH'
          },
          {
            value:'ETC',
            label:'ETC'
          },
          {
            value:'ZEC',
            label:'ZEC'
          },
          {
            value:'DOGE',
            label:'DOGE'
          },
          {
            value:'QTUM',
            label:'QTUM'
          },
          {
            value:'HSR',
            label:'HSR'
          },
          {
            value:'HC',
            label:'HC'
          },
          {
            value:'BOS',
            label:'BOS'
          },
          {
            value:'BTM',
            label:'BTM'
          }
        ],
        coinname:'BTC',
        coinimg:'../../static/img/btc@2x.png',
        imgarr:{
          BTC:{
            img:'../../static/img/btc@2x.png',
            hl:26953
          },
          ETH:{
            img:'../../static/img/eth@3x.png',
            hl:940
          },
          EOS:{
            img:'../../static/img/eos@2x.png',
            hl:17
          },
          USDT:{
            img:'../../static/img/usdt@2x.png',
            hl:6.79
          },
          LTC:{
            img:'../../static/img/ltc@2x.png',
            hl:4710.83
          },
          BCH:{
            img:'../../static/img/bch@2x.png',
            hl:1133.8
          },
          BSV:{
            img:'../../static/img/svg@2x.png',
            hl:439.3
          },
          TRX:{
            img:'../../static/img/trx@2x.png',
            hl:0.15
          },
          DASH:{
            img:'../../static/img/dash@2x.png',
            hl:618.25
          },
          ETC:{
            img:'../../static/img/etc@2x.png',
            hl:32.22
          },
          ZEC:{
            img:'../../static/img/zec@2x.png',
            hl:373.82
          },
          DOGE:{
            img:'../../static/img/icon_currency_doge@2x.png',
            hl:0.01
          },
          QTUM:{
            img:'../../static/img/qtum.png',
            hl:17.25
          },
          HSR:{
            img:'../../static/img/hsr@2x.png',
            hl:4.42
          },
          HC:{
            img:'../../static/img/hsr@2x.png',
            hl:9.24
          },
          BOS:{
            img:'../../static/img/bos_big@2x.png',
            hl:9.24
          },
          BTM:{
            img:'../../static/img/btm@2x.png',
            hl:0.12
          }

        },
        coinnum:1000,
        moneynum:26953000
      }
    },
    watch:{
      coinname (val) {
        this.moneynum = this.imgarr[val].hl * this.coinnum
        this.coinimg = this.imgarr[val].img
        console.log(val)
      },
      coinnum(val){
        this.moneynum = val*this.imgarr[this.coinname].hl
      }
    },
    methods:{
      getImage () {
        html2canvas(document.getElementById('canvas_box')).then(function(canvas) {
          var imgmap = canvas.toDataURL()
          if (window.navigator.msSaveOrOpenBlob) {
            var bstr = atob(imgmap.split(',')[1])
            var n = bstr.length
            var u8arr = new Uint8Array(n)
            while (n--) {
              u8arr[n] = bstr.charCodeAt(n)
            }
            var blob = new Blob([u8arr])
            window.navigator.msSaveOrOpenBlob(blob, 'chart-download' + '.' + 'png')
          } else {
            // 这里就按照chrome等新版浏览器来处理
            const a = document.createElement('a')
            a.href = imgmap
            a.setAttribute('download', 'chart-download')
            a.click()
          }
        });
      }
    }
  }
</script>

<style scoped>
  .app-box{
    width:100%;
  }
  html,body{
    width:100%;
    margin:0;
    padding:0;
  }
  .canvas_image_box{
    width:375px;
    height:670px;
    background:url('../assets/img@2x.png') no-repeat;
    background-size:100% 100%;
    position:relative;
    margin:0 auto;
    margin-bottom:20px;
  }
  .coinname{
    color:#fff;
    font-size:12px;
    position:absolute;
    top:32px;
    left:20px;
  }
  .money{
    font-family: Avenir-Medium;
    font-size: 26px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: left;
    position:absolute;
    top:117px;
    left:33px;
  }
  .money-box{
    left:22px;
    top:416px;
    position:absolute;
  }
  .logo-img{
    width:22px;
    height:22px;
    float:left;
    margin-right:10px;
  }
  .coin-name-font{
    font-family: PingFangSC-Medium;
    font-size: 13px;
    color: #333333;
    letter-spacing: 0;
    line-height:22px;
    float:left;
  }
  .money-gt-box{
    width:150px;
    position:absolute;
    right:30px;
    top:409px;
    text-align: right;
  }
  .money-first{
    font-family: PingFangSC-Regular;
    font-size: 15px;
    color: #333333;
    text-align: right;
  }
  .money-second{
    ont-family: PingFangSC-Regular;
    font-size: 11px;
    color: #999999;
    letter-spacing: 0;
    text-align: right;
  }
  .fool-btn{
    height:60px;
    width:80%;
    margin:0 auto;
    line-height:60px;
    text-align: center;
    color:#fff;
    background:#606c8b;
    margin-bottom:15px;
    cursor:pointer;
  }
  .alert-box{
    background:rgba(0,0,0,0.4);
    width:100%;
    height:100%;
    position:fixed;
    top:0;
    left:0;
  }
  .choose-box{
    height:300px;
    background:#fff;
    position:fixed;
    left:0;
    bottom:0;
    width:100%;
    overflow-y:auto;
  }
  .select-box{
    width:45%;
    display:inline-block;
    padding:0 5px;
  }
  .select-center-box{
    text-align: center;
    margin-bottom:20px;
  }
</style>


